<template>
  <el-pagination
    class="custom-pagination"
    background
    :layout="layout"
    :total="pageTotal"
    :page-size="pageSize"
    :current-page="pageNumber"
    :hide-on-single-page="singlePage"
    @size-change="handlePageSizeChange"
    @current-change="handlePageNumberChange"
  >
  </el-pagination>
</template>

<script lang="ts" setup>
withDefaults(
  defineProps<{
    pageTotal: number;
    pageNumber: number;
    pageSize: number;
    layout?: string;
    singlePage?: boolean;
  }>(),
  {
    pageTotal: 0,
    pageNumber: 0,
    pageSize: 20,
    singlePage: false,
    layout: "total, prev, pager, next, sizes",
  }
);
const emits = defineEmits<{
  (e: "emitPageSizeChange", value: number): void;
  (e: "emitPageNumberChange", value: number): void;
}>();
const handlePageSizeChange = (value: number) => {
  emits("emitPageSizeChange", value);
};
const handlePageNumberChange = (value: any) => {
  emits("emitPageNumberChange", value);
};
</script>
<style lang="scss" scoped>
.el-pagination.custom-pagination {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 10px;
  margin-top: 20px;

  :deep(li),
  :deep(.btn-prev),
  :deep(.btn-next) {
    min-width: 28px;
    height: 28px;
  }
}

:deep(.el-pagination__sizes .el-select__wrapper) {
  min-height: 34px;
}
</style>
